.card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    max-width: 250px;
    margin: auto;
    text-align: center;
    font-family: arial;
    border-radius: 25px;
    padding: 0px 0px 10px 0px;
}

a {
  color: rgb(0, 0, 0);
  background-color: transparent;
  text-decoration: none;
}

.card-img {
    object-fit: cover;
    width: 100%;
    height: 50%;
    border-radius: 25px 25px 0px 0px;
    display: grid;
    height: 200px;
    width:250px;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
}

.columns{
  width:33%;
  float:left;
  padding: 0px 0px 20px 0px;
}
